import {TaskItem} from '../types/types';

//     DD.MM.YYYY
export const isValidDate = (date: string, minDate?: Date): boolean => {
    if (!date) return true;

    //      
    const regex = /^\d{2}\.\d{2}\.\d{4}$/;
    if (!regex.test(date)) return false;

    const [day, month, year] = date.split('.').map(Number);

    //    
    if (day < 1 || day > 31) return false;
    if (month < 1 || month > 12) return false;
    if (year < 1900 || year > 2100) return false;

    //    Date 
    const dateObj = new Date(year, month - 1, day);
    const isValid = (
        dateObj.getDate() === day &&
        dateObj.getMonth() === month - 1 &&
        dateObj.getFullYear() === year
    );

    //   
    if (isValid && minDate) {

        const minDateWithoutTime = new Date(minDate);
        minDateWithoutTime.setHours(0, 0, 0, 0);

        return dateObj >= minDateWithoutTime;
    }

    return isValid;
};

//     HH:MM
export const isValidTime = (time: string): boolean => {
    if (!time) return true;

    const regex = /^\d{2}:\d{2}$/;
    if (!regex.test(time)) return false;

    const [hours, minutes] = time.split(':').map(Number);

    //  
    return hours >= 0 && hours <= 23 && minutes >= 0 && minutes <= 59;
};

export type TaskValidationErrors = {
    title: string[];
    description: string[];
    date: string[];
    time: string[];
};

//    
export const validateTask = (task: TaskItem): TaskValidationErrors => {
    const errors: TaskValidationErrors = {
        title: [],
        description: [],
        date: [],
        time: [],
    };

    const today = new Date();
    today.setHours(0, 0, 0, 0);

    //  
    if (!task.title.trim()) {
        errors.title.push('  ');
    }

    //  
    if (!task.description.trim()) {
        errors.description.push('  ');
    }

    //  
    if (!task.date.trim()) {
        errors.date.push('  ');
    } else {
        if (!isValidDate(task.date, today)) {
            if (!/^\d{2}\.\d{2}\.\d{4}$/.test(task.date)) {
                errors.date.push(',      DD.MM.YYYY');
            } else {
                const [day, month, year] = task.date.split('.').map(Number);
                const dateObj = new Date(year, month - 1, day);
                if (dateObj < today) {
                    errors.date.push('      ');
                } else {
                    errors.date.push(',   ');
                }
            }
        }
    }

    //  
    if (!task.time.trim()) {
        errors.time.push('  ');
    } else if (!isValidTime(task.time)) {
        errors.time.push(',      HH:MM');
    }

    return errors;
};
